@charset "UTF-8";
* {
  box-sizing: border-box;
}

body {
  font-family: “游明朝”, “Yu Mincho”, “游明朝体”, “YuMincho”, “ヒラギノ明朝 Pro W3”, “Hiragino Mincho Pro”, “HiraMinProN-W3”, “HGS明朝E”, “ＭＳ Ｐ明朝”, “MS PMincho”, serif;
}

.btn {
  display: inline-blick;
  width: 300px;
  max-width: 100%;
  padding: 20px 10px;
  border: 2px solid transparent;
  color: #fff;
  font-size: 1.125rem;
  text-align: center;
  text-decoration: none;
  transition: 0.25s;
}

.breadcrumb {
  margin-bottom: 98px;
}
.breadcrumb__item {
  display: inline;
}
.breadcrumb__item::after {
  content: ">";
  padding: 0 0.2em;
  color: #555;
}
.breadcrumb__item:last-child::after {
  content: "";
}
.breadcrumb__link {
  text-decoration: none;
  color: #000;
}
.breadcrumb__link:hover {
  text-decoration: underline;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .breadcrumb {
    margin-bottom: 58px;
  }
}
@media screen and (max-width: 767px) {
  .breadcrumb {
    margin-bottom: 60px;
  }
  .breadcrumb__item {
    font-size: 8px;
  }
}

.burger-btn {
  display: none;
  position: relative;
  width: 40px;
  height: 30px;
  cursor: pointer;
  z-index: 300;
  top: 14px;
}
.burger-btn .bar {
  display: inline-block;
  transition: all 0.5s;
  box-sizing: border-box;
}
@media screen and (max-width: 767px) {
  .burger-btn {
    display: block;
  }
}

.bar {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #8a8080;
  border-radius: 4px;
}
.bar_top {
  top: 0;
}
.bar_mid {
  top: 14px;
}
.bar_bottom {
  bottom: 0;
}

.burger-btn.active .bar_top {
  transform: translateY(10px) rotate(-45deg);
}

.burger-btn.active .bar_mid {
  opacity: 0;
}

.burger-btn.active .bar_bottom {
  transform: translateY(-20px) rotate(45deg);
}

