.modal {
  display: none;
  height: 100vh;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 11;
}
.modal__bg {
  background: rgba(0, 0, 0, 0.8);
  height: 100vh;
  position: absolute;
  width: 100%;
}
.modal-form {
  background: #fff;
  left: 50%;
  padding: 40px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 550px;
  height: 600px;
  color: #000;
  flex-direction: column;
}
.modal-form__title {
  font-size: 20px;
  line-height: 34px;
  text-align: center;
}
.modal-form__line {
  width: 80%;
  margin: 29.5px auto 33.5px auto;
  border: 1px solid #e7e7e7;
}
.modal-form__label {
  font-size: 16px;
  line-height: 27px;
  display: block;
}
.modal-form__select {
  margin: 14px 0;
  height: 32px;
  width: 80%;
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #e7e7e7;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color: #797878;
  text-indent: 1em;
  padding-right: 5px;
  background-color: #fff;
}
.modal-form__input {
  width: 80%;
  height: 32px;
  margin: 14px 0;
  box-sizing: border-box;
  border: 1px solid #e7e7e7;
  color: #797878;
  text-indent: 1em;
}
.modal-form__btn {
  margin: 64px auto 90px auto;
  text-align: center;
  border: 1px solid #707070;
  width: 198px;
  height: 42px;
  text-align: center;
  background-color: #fff;
  display: block;
}
.modal-form__submit {
  text-decoration: none;
  color: #000;
  font-size: 16px;
  line-height: 27px;
}
.modal-no_scroll {
  overflow: hidden;
}
@media screen and (max-width: 767px) {
  .modal__bg {
    width: 100%;
  }
  .modal-form {
    width: 75%;
    top: 40%;
    height: 70%;
    padding: 20px;
  }
  .modal-form__title {
    font-size: 18px;
  }
  .modal-form__line {
    margin-top: 15px;
    margin-bottom: 18px;
  }
  .modal-form__select {
