@media screen and (max-width: 767px) {
  .media {
    padding-top: 40px;
    padding-bottom: 20px;
  }
  .media__block {
    display: block;
    text-align: center;
    margin: 0 auto;
    padding-bottom: 60px;
    width: 90%;
  }
  .media__img {
    width: 100%;
    margin-bottom: 20px;
    height: 253.125px;
  }
  .media__title {
    font-size: 18px;
    line-height: 40px;
    margin-bottom: 20px;
  }
  .media__text {
    font-size: 8px;
    margin-bottom: 40px;
  }
  .media__btn {
    font-size: 12px;
    margin-bottom: 20px;
  }
}

.news {
  padding-top: 60px;
  padding-bottom: 60px;
}
.news__head {
  text-align: center;
}
.news__title {
  font-size: 36px;
  line-height: 32px;
  margin-top: 20px;
}
.news__nav {
  text-align: center;
  margin-top: 46px;
  margin-bottom: 49px;
  cursor: pointer;
  display: flex;
  justify-content: center;
}
.news__nav-item {
  color: black;
  margin-right: 21px;
  margin-left: 21px;
}
.news__nav-item.js-active {
  display: inline-block;
  border-bottom: 1px solid;
}
.news__wrapper {
  display: none;
}
.news__wrapper.js-show {
  display: block;
}
.news__inner {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
}
.news__item {
  height: 130px;
  width: 570px;
  display: flex;
  background-color: #f6f6f6;
  color: black;
}
.news__img {
  width: 103px;
  height: 100px;
  margin: 15px 30px 15px 10px;
}
.news__link {
  text-decoration: none;
  margin-top: 20px;
}
.news__link:nth-child(1), .news__link:nth-child(2) {
  margin-top: 0;
}
.news__time {
  margin-top: 15px;
  font-size: 14px;
  line-height: 32px;
}
.news__text {
  font-size: 16px;
  line-height: 32px;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .news__inner {
    justify-content: center;
  }
  .news__link:nth-child(2) {
    margin-top: 20px;
  }
}
@media screen and (max-width: 767px) {
  .news__inner {
    justify-content: center;
  }
  .news__item {
    width: 90vw;
    height: 90px;
  }
  .news__img {
    width: 62px;
    height: 60px;
    margin-bottom: 15px;
  }
  .news__logo {
    width: 30px;
    height: 30px;
  }
  .news__title {
    font-size: 25px;
  }
  .news__link:nth-child(2) {
    margin-top: 20px;
  }
  .news__text {
    font-size: 8px;
    line-height: 2em;
  }
  .news__time {
    font-size: 8px;
  }
}

.top {
  color: #fff;
  position: relative;
}
.top__title {
  position: absolute;
  top: 30%;
  left: 70%;
  writing-mode: vertical-rl;
  font: 800 34px/42px YuMincho;
  line-height: 3;
  z-index: 10;
  letter-spacing: 0.2em;
}
.top__img {
  height: 1079px;
  width: 100%;
  z-index: 1;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .top__img {
    height: 800px;
  }
}
@media screen and (max-width: 767px) {
  .top__title {
    top: 35%;
    left: 35%;
    font-size: 24px;
  }
  .top__img {
    height: 550px;
  }
}

@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
.swiper-slide-active .slide-img,
.swiper-slide-duplicate-active .slide-img,
.swiper-slide-prev .slide-img {
  animation: zoomUp 10s linear 0s 1 normal both;
}

.slide-img img {
  display: block;
}